<template>
  <v-main>
    <div class="pa-8"></div>
    <v-card width="600" class="mx-auto card">
      <div class="pa-6"></div>

      <v-form ref="form" lazy-validation>
        <v-row justify="center">
          <v-col cols="10" md="8">
            <v-text-field
              outlined
              rounded
              v-model="user.account"
              required
              clearable
            >
              <template v-slot:label>
                <p class="mx-4">帳號</p>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="10" md="8">
            <v-text-field
              type="password"
              outlined
              rounded
              v-model="user.password"
              required
              clearable
            >
              <template v-slot:label>
                <p class="mx-4">密碼</p>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="12">
            <p class="red--text text-center">{{ errorMsg }}</p>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col class="text-center mb-6" cols="12">
            <v-btn
              type="submit"
              depressed
              rounded
              elevation="0"
              :width="$vuetify.application.top + 200 + 'px'"
              :max-height="$vuetify.application.top + 'px'"
              :min-height="$vuetify.application.top - 50 + 'px'"
              @click.prevent="submit"
            >
              <span style="font-size: 24px">登入</span>
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card>
  </v-main>
</template>

<script>
import md5 from "js-md5";

export default {
  data: () => ({
    date: null,
    menu: false,
    user: {
      account: "",
      password: "",
    },
    errorMsg: "",
  }),
  methods: {
    submit() {
      if (this.user.account == "") {
        this.errorMsg = "請輸入帳號";
      } else if (this.user.password == "") {
        this.errorMsg = "請輸入密碼";
      } else if (this.user.account && this.user.password !== "") {
        const form_data = new FormData();
        form_data.append("account", this.user.account);
        form_data.append("password", md5(this.user.password));

        const api = `https://www.gr8088.com/api/login/do_login`;
        const vm = this;
        vm.$http
          .post(api, form_data)
          .then((result) => {
            console.log(result);

            if (result.data.success == "true") {
              localStorage.setItem("auth_token", result.data.auth_token);
              localStorage.setItem("bd_token", result.data.bd_token);
              vm.backHome();
            } else {
              if (result.data.error_msg == "密碼錯誤") {
                this.errorMsg = "密碼錯誤，請重新輸入";
              } else {
                this.errorMsg = "帳號或密碼有誤，請重新輸入";
              }
            }
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
    async backHome() {
      await this.$router.push({ name: "Home" });
      await location.reload();
    },

    click() {
      const array = new Set(this.arr);
      return array;
    },
  },
};
</script>

<style lang="css">
.input {
  background: linear-gradient(to right, #00a199 0%, #00e6b4 100%);
}

.login-btn span {
  text-transform: capitalize;
  font-weight: normal;
}
</style>